import React from 'react';
import 'jquery/dist/jquery';
import 'bootstrap/dist/js/bootstrap';
import 'bootstrap/dist/css/bootstrap.css';
import '../index.css';
import { NavLink } from 'react-router-dom';

const Navbar = () => {
  return (
    <div>
    <nav className="navbar navbar-expand-lg navbar-light">
    <NavLink className="navbar-brand" to="/">Pro</NavLink>
    
    <div className=" navbar-nav" id="navbarSupportedContent">
        <ul className="navbar-nav ml-auto">
            
        <li className="nav-item"
        >
            <button className="nav-link btns" > <NavLink to="/signup" style={{color: "rgb(241, 232, 232)"}}> Signup </NavLink></button>
        </li>
        <li className="nav-item"
        >
            <button className="nav-link btns" > <NavLink to="/login" style={{color: "rgb(241, 232, 232)"}}> Login </NavLink></button>
        </li>
            
        </ul>
    </div>
</nav>
    </div>
  )
}

export default Navbar