import React from 'react';
import { NavLink} from 'react-router-dom';
import './Login.css';

import {Button} from 'reactstrap';

const Signup = () => {
   
  return (
    <div>
    <div className="home text-center">
    <section className="sec1">
        <div className="div1 ">
            <div className="container contain-1">
                <div className="demo4">
                    <div className="div2">
                        <h1 className="text-center">Signup</h1>
                        <div className="div3">
                            <div className="username demo">
                                <h6>Your Name</h6>
                                <input type="text" className="demo1" placeholder="Enter your Name"/>
                            </div>
                            <div className="password demo">
                            <h6>Password</h6>
                                <input type="password" className="demo1" placeholder="Enter your password"/>
                            </div>
                            <div className="password demo">
                            <h6>Confirm Password</h6>
                                <input type="password" className="demo1" placeholder="Confirm your password"/>
                            </div>
                            <Button  className="btns text-center"style={{marginTop:"30px"}} > <a href="https://chhabriaheena.github.io/todolist/" style={{color: "rgb(241, 232, 232)"}}>Signup  </a></Button>


                            
     
                        </div>
                    </div>
                    
                </div>
            </div>
        </div>
    </section>
    </div>
     <div className="home text-center">
     <p>Already have an account?</p>
     <button className="btns text-center" > <NavLink to="/login" style={{color: "rgb(241, 232, 232)"}}> Login </NavLink> </button>
     </div>
    </div>
  )
}

export default Signup