import React from 'react';
import { NavLink } from 'react-router-dom';
import './Login.css';

const Login = () => {
  return (
    <div>
    <div className="home text-center">
    <section className="sec1">
        <div className="div1 ">
            <div className="container contain-1">
                <div className="demo4">
                    <div className="div2">
                        <h1 className="text-center">Login </h1>
                        <div className="div3">
                            <div className="username demo">
                                <h6>Username</h6>
                                <input type="text" className="demo1" placeholder="Enter your Username"/>
                            </div>
                            <div className="password demo">
                            <h6>Password</h6>
                                <input type="password" className="demo1" placeholder="Enter your password"/>
                            </div>
                            <h6 className="demo remember"><input type="checkbox"/> Remember me</h6>
                            <button  className="btns text-center"style={{marginTop:"30px"}} > <a href="https://chhabriaheena.github.io/todolist/" style={{color: "rgb(241, 232, 232)"}}>Login  </a></button>
</div>
                    </div>
                    
                </div>
            </div>
        </div>
    </section>
    <p>Don't have an account?</p>
    <button className="btns text-center" > <NavLink to="/signup" style={{color: "rgb(241, 232, 232)"}}> Signup </NavLink> </button>
    </div></div>
  )
}

export default Login